
.cell-container {
  height: 190px;
  background-color: #fff;
  position: relative;
}
.cell-container .cell-avator {
    position: absolute;
    left: 24px;
    top: 25px;
    width: 140px;
    height: 140px;
    border-radius: 10px;
}
.cell-container .cell-detail {
    padding-left: 188px;
}
.cell-container .cell-detail .cell-main {
      padding-top: 33px;
      font-size: 32px;
      color: #1e1e1e;
      height: 45px;
}
.cell-container .cell-detail .cell-sup {
      height: 37px;
      font-size: 26px;
      color: #9b9b9b;
}
.cell-container .cell-num {
    position: absolute;
    top: 45px;
    right: 29px;
    font-size: 24px;
    color: #9b9b9b;
}



