
.page-loading {
    text-align: center;
}
.page-loading-icon{
        vertical-align: middle;
        width: 32px;
        height: 32px;
        animation: rotate 1s ease-in-out infinite;
        border-radius: 0.0625rem;
        font-weight: 700;
        margin: 15px 0;
}
@keyframes rotate {
0% {
        transform: rotate(0deg);
}
100% {
        transform: rotate(360deg);
}
}
.list-donetip {
    text-align: center;
    height: 90px;
    line-height: 90px;
    font-size: 28px;
}
.list-container {
    height: 100%;
    overflow: auto;
}



